@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;700&display=swap);
*{
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

body{
  background-color: #EDF2F4;
}

.container{
  background-color: #7b8cde;
  width: 70%;
  margin: auto;
  margin-top: 4px;
  min-height: 100vh;
  padding: 4vmax 8vmax;
  box-sizing: border-box;
  border: 8px solid #e6ebed;
}

.container > form {
  display: flex;
  flex-direction: column ;
}

input,
textarea,
button {
  padding: 15px;
  border: 1px solid #EDF2F4;
  border-radius: 5px;
  margin: 10px 0;
}

button{
  border: none;
  cursor: pointer;
  background-color: #FFBF00;
  font-size: 1.2rem;
}

.container h1 {
  text-align: center;
  color: #07020D;
  margin: 30px;
  font-size: 2.4rem;
}

.task{
  display: flex;
  background-color: #EDF2F4;
  padding: 20px;
  border-radius: 5px;
  margin: 20px 0;
  align-items: center;
}

.task div{
  width: 100%;
}

.task button{
  border-radius: 100%;
  width: 30px;
  height: 30px;
  font-size: 1.6rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #D1462F;
}

.task button:hover{
  background-color: red;
}

.task p{
  font-weight: 600;
  color: #07020D;
}

.task span{
  font-weight: 300;
  color: #07020D;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

nav {
    background-color: #694966;
    color: #EDF2F4;
    padding: 18px;
    font-weight: 100;
}

